import { IManagedObject } from '@c8y/client';

export interface LoriotDevice {
  title: string;
  deveui: string;
  appeui: string;
  appkey: string;
  appid: string;
  deviceType: IManagedObject;
  error?: string;
  lnsConnectionName: string;
}

export interface Application {
  deviceLimit: number;
  devices: number;
  hexId: string;
  name: string;
  outputs: Array<any>;
}

export interface LoriotDeviceFormly extends LoriotDevice {
  connection: {
    name: string;
    description: string;
    baseUrl: string;
    username: string;
    password: string;
  };
  application: Application;
}

export const PRODUCT_EXPERIENCE = {
  EVENT: 'deviceRegistration',
  COMPONENT: 'loriot-registration',
  RESULT: { SUCCESS: 'registrationSuccess', FAILURE: 'registrationFailure' }
};
