import { Injectable } from '@angular/core';
import { TenantUiService } from '@c8y/ngx-components';
import { LoriotDeviceRegistrationButtonComponent } from './loriot-device-registration-button.component';
import { RegisterDeviceFactory, RegisterDeviceItem } from '@c8y/ngx-components/register-device';
@Injectable({
  providedIn: 'root'
})
export class LoriotDeviceRegistrationFactory implements RegisterDeviceFactory {
  constructor(private tenantService: TenantUiService) {}

  get() {
    const items: RegisterDeviceItem[] = [];
    if (this.tenantService.isMicroserviceSubscribedInCurrentTenant('loriot')) {
      items.push({
        template: LoriotDeviceRegistrationButtonComponent,
        priority: 97,
        category: 'single'
      } as RegisterDeviceItem);
    }
    return items;
  }
}
