import { Component } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { LoriotDeviceRegistrationComponent } from './loriot-device-registration.component';

@Component({
  selector: 'c8y-loriot-registration',
  templateUrl: 'loriot-device-registration-button.component.html'
})
export class LoriotDeviceRegistrationButtonComponent {
  constructor(private modalService: BsModalService) {}

  open() {
    this.modalService.show(LoriotDeviceRegistrationComponent, {
      class: 'modal-sm',
      ariaDescribedby: 'modal-body',
      ariaLabelledBy: 'modal-title',
      ignoreBackdropClick: true
    });
  }
}
