import { Injectable } from '@angular/core';
import {
  FetchClient,
  IFetchOptions,
  IManagedObject,
  InventoryService,
  IResult,
  IResultList
} from '@c8y/client';
import { TranslateService } from '@ngx-translate/core';
import { gettext, AppStateService } from '@c8y/ngx-components';
import { Application, LoriotDevice } from './loriot-device-registration.model';

export enum ErrorName {
  NoDeviceProtocolsError = 'NoDeviceProtocolsError',
  NoConnectivitySettingsError = 'NoConnectivitySettingsError',
  ConnectivitySettingsError = 'ConnectivitySettingsError',
  RegistrationError = 'RegistrationError',
  DeviceProtocolsFetchError = 'DeviceProtocolsFetchError',
  ApplicationError = 'ApplicationError'
}

@Injectable({
  providedIn: 'root'
})
export class LoriotProviderService {
  private readonly baseUrl: string = '/service/loriot/';
  private readonly registrationUrl: string = `${this.baseUrl}newDeviceRequest`;
  private readonly header: any = { 'Content-Type': 'application/json' };
  private readonly applicationsUrl: string = `${this.baseUrl}applications`;
  constructor(
    private client: FetchClient,
    private inventoryService: InventoryService,
    private translateService: TranslateService,
    private appState: AppStateService
  ) {}
  async getConnections() {
    const options: IFetchOptions = {
      method: 'GET',
      headers: this.header
    };
    const res = await this.client.fetch(`${this.baseUrl}lns-connection`, options);
    const data = await res.json();

    if (res.status === 200) {
      if (data.length === 0) {
        await this.throwNoConnectivitySettingsError();
      }
    } else {
      await this.throwConnectivitySettingsError(data);
    }
    return { res, data };
  }

  async createDevice(device: LoriotDevice): Promise<IResult<LoriotDevice>> {
    const options: IFetchOptions = {
      method: 'POST',
      headers: this.header,
      body: JSON.stringify(device)
    };
    const res = await this.client.fetch(this.registrationUrl, options);
    const data = await res.json();

    if (res.status !== 201) {
      this.throwRegistrationError(data);
    }
    return { res, data };
  }

  async getAvailableProtocols(
    filter: object = { withTotalPages: true }
  ): Promise<IResultList<IManagedObject>> {
    const query = {
      __filter: {
        __and: [
          { __has: 'c8y_IsDeviceType' },
          {
            type: { __in: ['c8y_LoraDeviceType', 'c8y_LpwanDeviceType'] }
          }
        ]
      },
      __orderby: [{ name: 1 }]
    };
    const deviceProtocolsList = await this.inventoryService.listQuery(query, filter);
    const { res, data } = deviceProtocolsList;
    if (res.status === 200) {
      if (data.length === 0) {
        this.throwNoDeviceProtocolsError();
      }
    } else {
      this.throwDeviceProtocolsFetchError();
    }

    return deviceProtocolsList;
  }

  async getApplications(connectionName: string): Promise<IResultList<Application>> {
    const options: IFetchOptions = {
      method: 'GET',
      headers: this.header,
      params: {
        loriotConnectionName: connectionName
      }
    };
    const res = await this.client.fetch(this.applicationsUrl, options);

    const data = await res.json();
    /* Every connection will have atleast 1 application,
    so having check for this enpoint returning empty result is not needed*/
    if (res.status !== 200) {
      this.throwApplicationError(data);
    }
    return { res, data };
  }

  private async throwNoConnectivitySettingsError() {
    const error = new Error();
    error.name = ErrorName.NoConnectivitySettingsError;
    const hasAdminRight = await this.appState.isApplicationAvailable('administration');
    if (hasAdminRight) {
      error.message = this.translateService.instant(
        gettext(
          `Connectivity settings are not configured. Configure them in the Administration application under <a href="{{ link }}">Settings</a>.`
        ),
        {
          link: '/apps/administration/index.html#/connectivitySettings/multiple_lns_connectors_loriot'
        }
      );
    } else {
      error.message = gettext(
        'Connectivity settings are not configured. Contact the administrator.'
      );
    }

    throw error;
  }

  private throwConnectivitySettingsError(data: { message: string }) {
    const error = new Error();
    error.name = ErrorName.ConnectivitySettingsError;
    error.message = data.message;
    throw error;
  }

  private throwRegistrationError(data: { message: string }) {
    const error = new Error();
    error.name = ErrorName.RegistrationError;
    error.message = data.message;
    throw error;
  }

  private throwDeviceProtocolsFetchError() {
    const error = new Error();
    error.name = ErrorName.DeviceProtocolsFetchError;
    error.message = gettext('Could not load device protocols.');
    throw error;
  }

  private throwNoDeviceProtocolsError() {
    const error = new Error();
    error.name = ErrorName.NoDeviceProtocolsError;
    error.message = this.translateService.instant(
      gettext(
        `No device protocols configured. Create a LoRa device protocol in <a href="{{ link }}">Device protocols</a>.`
      ),
      {
        link: '/apps/devicemanagement/#/deviceprotocols'
      }
    );
    throw error;
  }

  private throwApplicationError(data: { message: string }) {
    const error = new Error();
    error.name = ErrorName.ApplicationError;
    error.message = data.message;
    throw error;
  }
}
